let _self;
export default {
	data() {
		return {
			isok: true,
			isPlan: false,
			noTime: false,
			msgErr: false,
			dont: false,
			other: false
		};
	},
	onLoad() {
		_self = this;
	},
	methods: {
		reOrder() {},
		plan() {
			_self.isPlan = !_self.isPlan;
		}
	}
};
